.bg-image {
    background-image: url(/static/media/laptop.d3868f40.jpg);
    background-size: cover;
    margin-bottom: 0;
}

.container {
    background-color: rgb(255, 255, 255, 0.4);
    border-radius: 1.5em;
    padding: 2em;
    color: black;
}

.icon-links {
    color: black;
    font-size: 2.4em;
}

.name {
    font-family: 'Philosopher', sans-serif;
}

.lead {
    font-size: 1.6em;
}
.pic {
    border-radius: 45%;
    -moz-border-radius: 45%;
    -webkit-border-radius: 45%;
    width: 15em;
}

.about-page {
    background-image: url(/static/media/sunset.05586c00.jpg);
    background-size: cover;
}

.about-container {
    background-color: transparent;
}

.brand-statement {
    font-size: 1.2em;
    color: white;
}
.card {
    margin: 0.3em;
    padding: 0;
}

.project-card {
    background-color: #FDC88F;
    border-radius: 2em;
}

.project-image {
    border-top-left-radius: 2em;
    border-top-right-radius: 2em;
}
.project-page {
  background-image: url(/static/media/robert-horvick.6e415727.jpg);
  background-size: cover;
  margin-top: 0;
}

.project-container {
  background-color: transparent;
  padding-top: 0;
  margin-left: 3em;
  margin-right: 3em;
}

.header {
  font-size: 3em;
}
li.borderless {
  border: 0 none;
  background-color: rgb(255, 255, 255, 0.7);
}

.contact-area {
  background-image: url(/static/media/claudio-schwarz.0691cb63.jpg);
  background-size: cover;
}

.contact-container {
  padding-top: 7em;
  padding-bottom: 9em;
  background-color: rgb(255, 255, 255, 0.2);
}

.contact-form {
  background-color: rgb(255, 255, 255, 0.8);
}

.resume {
  color: black;
}
h2, h3, h4, h5 {
    font-family: 'Candal', sans-serif;
}

body {
    font-family: 'Alice', serif;
}
